suspend fun sec4(pod: Podcast) {

   document.body?.append {
      div("sec sec4") {

         div {
            id = "speechAudio"
            audio {
               controls = true;
            }
         }







         GlobalScope.launch {


            val tts = TextToSpeech.createOrThrowSync()

            val podcastwords = pod.sectionsContents.values.joinToString()

            tts.say(podcastwords)



         }


      }

   }
}