package ui.secs

import kotlinx.browser.document
import kotlinx.html.*
import kotlinx.html.dom.append
import kotlinx.html.js.div
import models.podcast.Podcast
import org.w3c.dom.HTMLElement
suspend fun sec2(pod: Podcast) {


   document.body?.append {
      div("sec sec2") {
         style = ""


         h4 { +"Podcast details:" }
         blockQuote {


            div("sectionsResponse") {
               style = " padding: 0.2em .45em .2em .45em; font-family: monospace; color:#113;"

               ol {
                  pod.sections.forEach {
                     li { +it }
                  }
               }

            }
            br();br();


         }


      }
   }


   sec3(pod);


}
