package ui

import kotlinx.browser.document
import kotlinx.html.*
import kotlinx.html.js.onKeyUpFunction
import misc.State


fun FlowContent.spanEditable(
   varKey :String,
   minWidthEm :Double = 1.0,
   maxWidthEm :Double = 10.0,
   classes:String? = "spanInput",
   block: SPAN.()->Unit
) =
   span(classes) {
      id = "inputSpan_${varKey}"
      attributes["enterkeyhint"] = "next"
      contentEditable = true;
      style = """
           display: inline-block;
           min-width: ${minWidthEm}em; max-width: ${maxWidthEm}em;
           box-shadow: 
                inset 0.5px 0.5px 0 0.75px rgb(1 1 1 / 20%), 
                inset 1.5px 1.5px 9px 4px #afb28028;
            padding: 0 .25em 0 .25em;
           """
      onKeyUpFunction = { ev->
         val innerText =
            document.getElementById(id)?.textContent?.replace("[><{}()]".toRegex(),"")
         State[varKey] = innerText.toString();

      }
      block();
   }



fun FlowContent.FillInTheBlank(input: String) {

   //match variable regex
   val blankPattern = Regex("___([a-zA-Z0-9]+),([0-9]+)___")

   var currentIndex = 0
   blankPattern.findAll(input).forEach { match ->
      // append text before the current blank placeholder
      val textBefore = input.substring(currentIndex, match.range.first)
      text(textBefore)

      // get the name and width of the blank placeholder
      val (name, width) = match.destructured

      // create the input field with required width
      spanEditable(name, minWidthEm = width.toDouble()) {}

      currentIndex = match.range.last + 1 // update current index
   }

   // append the remaining text after the last blank placeholder
   val textAfter = input.substring(currentIndex)
   text(textAfter)
}