suspend fun sec3(pod: Podcast) {
   GlobalScope.async {
      pod.getSectionContents()
   }.await()


   document.body?.append {
      div("sec sec3") {


         div("sec sec3") {
            style = ""


            blockQuote("podcastTextBlock") {
            pod.sectionsContents.forEach {

                  +it.value
                  br();


            }
            }


            br();

            GlobalScope.launch {
               sec4(pod);
            }

         }

      }
   }

}



