import kotlinx.html.FlowContent
import kotlinx.html.div
import kotlinx.html.style
import kotlin.js.Json

fun Json.toJsonString() = JSON.stringify(this);

fun String.getListItemsFromOL(): List<String> {
   val pattern = Regex("""\d+\.\s+(.+)""")
   return pattern.findAll(this)
      .map { it.groupValues[1] }
      .toList()
}



fun FlowContent.loadingDots(sstyle :String = "margin: .3em 1em .3em 1em") =
   div("dot-collision") {
   style = sstyle
}