import kotlinx.browser.window
import kotlinx.coroutines.await
import org.w3c.fetch.Headers
import org.w3c.fetch.RequestInit
import kotlin.js.json

class Requester(
   val key :String = window.atob("c2stWXpWa2xiOTVGdHYwVW54ZDNUbDZUM0JsYmtGSjRYT2Zibk1vVHEzOFFFc0VQV0tJ")
){

   suspend fun getCompletion(
      prompt :String,
      model :String = "text-ada-001",
      maxTokens :Int = 200,
      frequencyPenalty :Double = 0.45,
      presencePenalty :Double = 1.6,
   ): String {


      //build request body
      val bodyRaw = newCompletionBody(model, prompt, maxTokens, frequencyPenalty, presencePenalty)

      //RequestInit (options)
      val reqOptions = RequestInit(
         method = "POST",
         headers = newHeaders(),
         body = bodyRaw
      )

      //fetch and await
      val response = window.fetch("https://api.openai.com/v1/completions", reqOptions)
      val responseStr = response.await().text().await()
      console.log("response received:\n$responseStr");
      val choiceresult = (JSON.parse<String?>(responseStr).asDynamic()).choices[0].text

      return choiceresult

   }

   private fun newHeaders() = Headers().apply {
      append("Content-Type", "application/json")
      append("Authorization", "Bearer ${key}")
   }
   private fun newCompletionBody(
      model: String,
      prompt: String,
      maxTokens: Int,
      frequencyPenalty: Double,
      presencePenalty: Double
   ) = json(
      "model" to model,
      "prompt" to prompt,
      "temperature" to 0.75,
      "max_tokens" to maxTokens,
      "top_p" to 1,
      "frequency_penalty" to frequencyPenalty,
      "presence_penalty" to presencePenalty,
      "stop" to arrayOf("""\n\n""")
   ).toJsonString()
}