package models.podcast

import Requester
import getListItemsFromOL
import kotlinx.browser.window

import misc.State


data class Podcast (
   val adjectives :String,
   val topic      :String,
   val numSections   :Int,
) {

   val sections: MutableList<String> = mutableListOf()
   val sectionsContents: MutableMap<Int, String> = mutableMapOf()



   fun prompt(): String = """
# Generate a(n) ${adjectives} text-to-speech podcast.

### PODCAST TOPIC:
> ${topic}

### NUMBER OF SECTIONS:
> ${numSections}

### Numbered list of section titles:
"""


   fun sectionPrompt(secNum: Int): String {

      var numberedSectionsstr = ""
      sections.forEachIndexed { i, s ->
         numberedSectionsstr += "$i. $s\n"
      }

      return """
#Generate a(n) ${adjectives} text-to-speech, conversational podcast.

## Topic:
${topic}

## Sections
$numberedSectionsstr

---

# Part $secNum: ${sections[secNum]} (250+ words):
"""

   }

   suspend fun initSections() {


      //fetch
      val r =
         Requester(window.atob("c2stWXpWa2xiOTVGdHYwVW54ZDNUbDZUM0JsYmtGSjRYT2Zibk1vVHEzOFFFc0VQV0tJ")).getCompletion(
            this@Podcast.prompt(),
            "text-curie-001",
            200,
            State["dblFrequncePenalty"]?.toDouble() ?: 0.5,
            State["dblPresencePenalty"]?.toDouble() ?: 1.5
         )

      this.sections.addAll(r.getListItemsFromOL())

   }


   suspend fun getSectionContents() {

      sections.forEachIndexed { i, sec ->

         //fetch
         val r =
            Requester(window.atob("c2stWXpWa2xiOTVGdHYwVW54ZDNUbDZUM0JsYmtGSjRYT2Zibk1vVHEzOFFFc0VQV0tJ")).getCompletion(
               this@Podcast.sectionPrompt(i),
               "text-curie-001",
               200,
               State["dblFrequncePenalty"]?.toDouble() ?: 0.6,
               State["dblPresencePenalty"]?.toDouble() ?: 0.8
            )


         this.sectionsContents.put(i, r);
      }

   }

}


