package ui.secs

import Requester
import kotlinx.browser.document
import kotlinx.coroutines.GlobalScope
import kotlinx.coroutines.launch
import kotlinx.html.*
import kotlinx.html.js.*
import kotlinx.html.js.onInputFunction
import misc.State
import kotlinx.html.dom.append
import loadingDots
import models.podcast.Podcast
import ui.FillInTheBlank


object Sec1 {

   suspend fun create() {

      document.body?.append {


         h2 { +"Start a podcast:" };
         br();
         div("sec sec1") {


            blockQuote {
               FillInTheBlank("Generate a(n) ___adjectives,2___ podcast about ___topic,3___,")
               br();br();
               FillInTheBlank(" with ___numSections,1___ sections (default: 7)")
               br();br();


               advancedOptionsCollapsible()


               br();br(); br();br();




               // 'next' button
               button {
                  +"next"
                  onClickFunction = {

                     br()
                     loadingDots()

                     GlobalScope.launch {



                        //make podcast object
                        val pod =
                           Podcast(State["adjectives"] ?: "", State["topic"] ?: "", State["numSections"]?.toInt() ?: 0)

                        //http request made
                        pod.initSections()

                        document.body?.append {
                           GlobalScope.launch {
                              sec2(pod)
                           }
                        }

                        document.querySelector(".dot-collision")?.remove()



                     }
                  }
               }
            };
         }
      }
   }

   private fun FlowContent.advancedOptionsCollapsible() {
      div {
         style =
            " transform: scale(0.9);line-height: 0.85; color: #111; font-family: 'Rubik';  font-size: 65%; box-shadow: inset -.5px .5px .8px 1.3px rgba(1,1,1,0.15); margin: 1em; padding: .5em 0.35em 1em .8em; max-width: 300px; "
         h5 {
            style = "font-size: 125%;font-weight: bold; cursor: pointer; padding: 0.36em; margin: .1em 0 .2em 0; width: 75%;"
            +" ^ advanced"



            onClickFunction = { ev ->
               val targ = ev.target
               val els = targ.asDynamic().parentNode.children
               for (i in 1..els.length) {

                  val el = els[i]

                  if (el?.style.display != null  &&  el.style.display.toString().contains("none"))
                     el.style.display = "inline-block"
                  else
                     el.style.display = "none"
               }

            }
         }
         br { style = "display:none;" }

         //TODO: could abstract these 2 ranges into a component

         // frequence penalty:
         i { +"frequence penalty:"; style = "display:none;" }
         br { style = "display:none;" }
         rangeInput {
            style = "margin:0;width:100%;display:none;"
            value = "0.5"
            min = "0";
            max = "2";
            step = "0.1";
            onInputFunction = { ev ->
               val t = ev.target.asDynamic()
               t.nextElementSibling.value = t.value

               State["dblFrequncePenalty"] = t.value.toString();

            }
            State["dblFrequncePenalty"] = "0.5"
         }
         output() { +"0.5"; style = "display:none;" }
         br { style = "display:none;" }


         // presence penalty:
         i { +"presence penalty:"; style = "display:none;" }
         br { style = "display:none;" }
         rangeInput {
            style = "margin:0;width:100%;display:none;"
            value = "1.5"
            min = "0.0";
            max = "2.0";
            step = "0.1";
            onInputFunction = { ev ->
               val t = ev.target.asDynamic()
               t.nextElementSibling.value = t.value

               State["dblPresencePenalty"] = t.value.toString();

            }
            State["dblPresencePenalty"] = "1.5";
         }
         output() { +"1.5"; style = "display:none;" }

      }

   }


}